
import Image from "next/image";
import React from "react";
import react, {useState, useEffect} from"react"



const AuthPage = () => {
    const [type, setTyoe] = useState<"login" |"register">("login")
    const isLogin = type ==="login"
  return (
    <div className="min-h-screen flex items-center justify-center bg-gray-100 px-4 py-12">
      <div className="grid grid-cols-1 md:grid-cols-2 w-full max-w-7xl bg-white shadow-2xl rounded-2xl overflow-hidden">
        <div className="p-8 sm:p-12 min-h-[750px] flex flex-col justify-center">
          <h2 className="text-3xl font-bold text-gray-900 mb-2 ">{isLogin ? "SIgn In" : "Sign Up"}</h2>
            <p className="text-sm text-gray-600 mb-4 ">
          welcome back!

            </p>
            <form action="" className="space-y-4">
                <div>
                    <label htmlFor="email" className="block text-sm font-medium text-gray-700">
                        email
                    </label>
                    <input type="email" placeholder="johndoe@gmail.com" className="w-full mt-1 px-4 py-2 border rounded-md border-gray-400"></input>
                </div>
                <div>
                    <label htmlFor="email" className="block text-sm font-medium text-gray-700">
                        password
                    </label>
                    <input type="password" placeholder="********" className="w-full mt-1 px-4 py-2 border rounded-md border-gray-400"></input>
                </div>
                <button type="submit" className=" w-full py-2 px-4 rounded flex items-center justify-center gap-2 bg-indigo-600 hover:bg-indigo-700 text-white">lets explore</button>
                <p className="mt-6 text-sm text-center text-gray-500">
                    dont have an account?{" "}
                    <button className="text-indigo-500 hover:underline "> Sign Up</button>
                </p>

            </form>

        </div>
        <div className="hidden md:block bg-indigo-600 relative min-h-[750px] w-full">
            <Image src="/images/auth-img.png" alt="banner" fill 
            className="object-cover"/>
        </div>
      </div>
    </div>
  );
};

export default AuthPage;
